import React from 'react';
import {Field, InjectedFormProps, reduxForm} from 'redux-form';
import {FormControl} from '../comman/formControls/FormControls';
import {requiredField} from '../../utils/validators/validators';
import {LoginRequestData} from '../../api/api';
import S from './../comman/formControls/FormControls.module.css'

const LoginForm: React.FC<InjectedFormProps<LoginRequestData>> = ({handleSubmit, error, }) => {
    return (
        <form onSubmit={handleSubmit}> {/* 3 : 1- e.preventDefault,
        2-         ,    onSubmit(obj  )*/}
            <div>
                {/*Field      component  ,      .   
                onChange  input.    */}
                <Field placeholder={'email'}
                       name={'email'}
                       tagName="input"
                       component={FormControl}
                       validate={[requiredField]}/>
            </div>
            <div>
                <Field placeholder={'Password'}
                       name={'password'}
                       type={'password'}
                       tagName="input"
                       component={FormControl}
                       validate={[requiredField]}/>
            </div>
            <div>
                <Field type={'checkbox'} name={'rememberMe'} component={'input'}/> remember me
            </div>
            {error && <div className={S.formCommonError}>{error}</div>}
            <div>
                <button>Login</button>
            </div>
        </form>
    );
};

export const LoginReduxForm = reduxForm<LoginRequestData>({
    form: 'login'
})(LoginForm)
//  ,        -.
//     form  globalState   login:    -  ,
//    fields, values  :     Field (login, password, rememberMe)
